import { createStore } from "vuex";
import axios from "axios";



export default createStore({
  namespaced: true,
  state: {
    apiKey: "ff73598636b772c5fca88d178c32f1a4",
    upcoming: null,
    upcomingPath: 'https://api.themoviedb.org/3/movie/popular',
  },
  getters: {
    getUpcomingArray: (state) => state.upcoming,
  },
  mutations: {
    getUpcoming(state, payload) {
      state.upcoming = payload;
    },
    
  },
  actions: {
    async getUpcoming({ commit, state }) {
      try {
        const res = await axios.get(
          `${state.upcomingPath}?api_key=${state.apiKey}&language=en-US&page=1`
        );
        const result = res.data.results;
        commit("getUpcoming", result);
        // console.log(result);
      } catch (error) {
        console.log(error);
      }
    },
  },
  modules: {
   
  },
});
