  methods: {
    increment() {
      if (this.counter == 1) {
        this.counter++;
        this.link = this.images.image1
        
      } else {
        this.counter = this.counter;
      }
    },
    decrement() {
      if (this.counter == 2) {
        this.counter--;
       
        this.link = this.images.image
      } else {
        this.counter = this.counter;
      }
    },
  },
};
</script>




