<template>
  <div class="navbar section">
    <nav class="navbar-bar">
      <router-link to="/" class="navbar-bar__logo">EL ISKO</router-link>

      <ul
        class="navbar-bar__list"
        :class="{ 'navbar-bar__list--open': isMenuOpen }"
      >
        <li class="navbar-bar__list---item">
          <router-link
            to="/"
            class="navbar-bar__list---item_link home"
            :class="{ activelink: active === 'home' }"
            @click="isActive('home')"
          >
            Home
          </router-link>
        </li>
        <li class="navbar-bar__list---item">
          <router-link
            to="/aboutus"
            class="navbar-bar__list---item_link about"
            :class="{ activelink: active === 'aboutus' }"
            @click="isActive('aboutus')"
          >
            About us
          </router-link>
        </li>
        <li class="navbar-bar__list---item">
          <router-link
            to="/portfolio"
            class="navbar-bar__list---item_link portfolio"
            :class="{ activelink: active === 'portfolio' }"
            @click="isActive('portfolio')"
          >
            Portfolio
          </router-link>
        </li>
        <li class="navbar-bar__list---item">
          <router-link
            to="/news"
            class="navbar-bar__list---item_link new"
            :class="{ activelink: active === 'news' }"
            @click="isActive('news')"
          >
            News
          </router-link>
        </li>
        <button class="navbar-bar__list---item___button">
          <router-link to="/contact" @click="startRedirectionTimer"
            >Contact us</router-link
          >
        </button>
      </ul>
    </nav>
    <button class="navbar-burger" @click="toggleMenu">
      <span></span>
      <span></span>
      <span></span>
    </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: "home",
      active: "",
      isMenuOpen: false,
      numbers:0,
    };
  },
  methods: {
    isActive(item) {
      this.active = item;
    },
    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen;
      if(this.isMenuOpen){
        this.$router.push("menu");
      }else{
        this.isMenuOpen = false
        this.$router.go(-1);
      }
    },
    startRedirectionTimer() {},

    isActive(routeName) {
      this.active = routeName;

      const links = document.getElementsByClassName(
        "navbar-bar__list---item_link"
      );
      for (let i = 0; i < links.length; i++) {
        const currentLink = links[i];
        if (currentLink.getAttribute("to") !== `/${routeName}`) {
          currentLink.classList.add("non");
          currentLink.classList.remove("activelink");
        }
      }
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/common/_mixin.scss";

.navbar-burger {
  display: none;
}

@media (max-width: 768px) {
  .navbar-burger {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    position: absolute;
    width: 30px;
    height: 25px;
    background: transparent;
    border: none;
    top: 55px;
    right: 40px;
    border: none;
    cursor: pointer;
  }

  .navbar-burger span {
    // position: absolute;
    width: 100%;
    height: 3px;
    background-color: rgb(255, 255, 255);
    transition: transform 0.3s, opacity 0.3s;
  }

  .navbar-burger span:nth-child(1) {
    top: 0;
  }

  .navbar-burger span:nth-child(2),
  .navbar-burger span:nth-child(3) {
    top: 8px;
  }

  .navbar-burger span:nth-child(3) {
    width: 70%;
  }

  .navbar-bar__list {
    display: none !important;
  }

  .navbar-bar__list--open {
    display: block;
  }
}

.activelink {
  transition: 0.3s;
}

.activelink::before {
  transform: translateX(0);
  opacity: 1;
  display: flex;
  position: absolute;
  content: "";
  width: 5px;
  bottom: -7px;
  left: 20px;
  //    z-index: 5;
  height: 4px;
  border-radius: 20px;
  background: rgba(220, 112, 0, 1);
}

.activelink::after {
  display: flex;
  opacity: 1;
  position: absolute;
  content: "";
  transform: translateX(0);
  width: 31px;
  bottom: -7px;
  left: -15px;
  //    z-index: 5;
  height: 4px;
  border-radius: 20px;
  background: rgba(220, 112, 0, 1);
}

.navbar {
  width: 100%;

  body {
    background: BLACK;
  }

  &-bar {
    @include container();
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    &__logo {
      font-weight: 800;
      font-size: 30px;
      font-family: $p;
    }

    &__list {
      display: flex;
      align-items: center;
      gap: 60px;

      &---item {
        &___button {
          font-family: $p;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          position: relative;
          border: none;
          outline: none;
          padding: 10px 28px;
          border-radius: 6px;
          transition: 0.3s;
 
          transition: 0.3s;
          background: rgba(250, 144, 33, 1);
          &:hover {
            background: white;
           a{
            color: black;
            box-shadow: 0 0 100px white;
           } 
          }
        }

        // width: 100%;
        font-family: $p;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        position: relative;
        transition: 0.3s;

        &::before {
          transition: 0.3s;
          display: flex;
          opacity: 0;
          transition: 0.3s;
          transform: translateX(+30px);
          position: absolute;
          content: "";
          width: 5px;
          bottom: -7px;
          left: 17px;
          //    z-index: 5;
          height: 4px;
          border-radius: 20px;
          background: rgba(220, 112, 0, 1);
        }

        &::after {
          transition: 0.3s;
          display: flex;
          opacity: 0;
          transition: 0.3s;
          transform: translateX(-30px);
          position: absolute;
          content: "";
          width: 31px;
          bottom: -7px;
          left: -12px;
          //    z-index: 5;
          height: 4px;
          border-radius: 20px;
          background: rgba(220, 112, 0, 1);
        }
      }
    }
  }

  padding: 55px;
}

@media (max-width: 660px) {
  .navbar {
    padding: 50px 0;
  }
}
@media (max-width: 540px) {
  .header-text__item---h1{
    font-size: 18px !important;
  }
  .header-text__item---p{
    font-size: 12px !important;
  }
}
</style>
