          <h2>Say hello</h2>
          <p>Lorem Ipsum is simply dummy text of the printing .</p>
        </div>
        <div class="contact-blank-wrapper__form">
          <div
            class="contact-blank-wrapper__form---names contact-blank-wrapper__form---item"
          >
            <div>
              <p>First name</p>
              <input class="contact-inputs" type="text" v-model="firstName" />
            </div>
            <div>
              <p>Last name</p>
              <input class="contact-inputs" type="text" v-model="lastName" />
            </div>
          </div>
          <div
            class="contact-blank-wrapper__form---email contact-blank-wrapper__form---item"
          >
            <p>Email Address</p>
            <input
              class="contact-inputs"
              type="email"
              name="email"
              id=""
              v-model="emailAddress"
            />
          </div>
          <div
            class="contact-blank-wrapper__form---message contact-blank-wrapper__form---item"
          >
            <p>Message</p>
            <textarea
              class="contact-inputs"
              type="text"
              name=""
              id=""
              size="30"
              placeholder="Message"
              v-model="message"
            />
          </div>
