</template>

<script>
import { addDoc, collection } from "firebase/firestore";
import { db } from "../firebase";
import CurrentLink from "@/components/CurrentLink.vue";
export default {
  components: { CurrentLink },
  data() {
    return {
      currentLink: "Contact",
      headerText:'My name is Iskander Akhmedov and here you can fill a blank for me to contact you',
      headerUnderText:'If you want to work with fill the blank or go to Footer',
      isHover: false,
      firstName: "",
      lastName: "",
      emailAddress: "",
      message: "",
      isValid: false,
      problemWith: "",
    };
  },

  methods: {
    setItems() {
      if (
        this.firstName != "" &&
        this.lastName != "" &&
        this.emailAddress != "" &&
        this.emailAddress != "" &&
        this.emailAddress != ""
      ) {
        if (this.emailAddress.includes("@")) {
          if (this.emailAddress.includes(".")) {
            const userData = {
              firstName: this.firstName,
              lastName: this.lastName,
              emailAddress: this.emailAddress,
              message: this.message,
            };
            addDoc(collection(db, "contactus"), {
              firstName: userData.firstName,
              lastName: userData.lastName,
              emailAddress: userData.emailAddress,
              message: userData.message,
            });
            this.firstName = "";
            this.lastName = "";
            this.emailAddress = "";
            alert("Succesfully subscribed! Thank's for your support");
            return localStorage.setItem("contactus", JSON.stringify(userData));
          } else {
            alert("Your email has to have '.' inside");
          }
        } else {
          alert("Your email has to have @ inside");
        }
      } else {
        alert("Please fill all of the blanks");
      }
    },
  },
  created() {},
};
</script>

