<script >
import Header from "@/components/aboutus/Header.vue";
import Reasons from "@/components/aboutus/Reasons.vue";
import Banner from "@/components/aboutus/Banner.vue";
import Contact from "@/components/Contact.vue"
import Footer from "@/components/Footer.vue"
export default {
  components: { Header,Reasons,Banner,Contact,Footer },
  data() {
    return {
      counter: 1,
      images: {
        image: require("../components/aboutus/images/image.png"),
        image1: require("../components/aboutus/images/image2.png"),
      },
      link: require("../components/aboutus/images/image.png"),
    };
  },
  methods: {
    increment() {
      if (this.counter == 1) {
        this.counter++;
        this.link = this.images.image1;
      } else {
        this.counter = this.counter;
      }
    },
    decrement() {
      if (this.counter == 2) {
        this.counter--;

        this.link = this.images.image;
      } else {
        this.counter = this.counter;
      }
    },
  },
};
</script>

