<template>
  <div class="news" v-if="!isModalOpen">
   <CurrentLink
   :currentLink="currentLink"
   :headerText="headerText"
   :headerUnderText="headerUnderText"
   />
    <div class="news-wrapper container2">
      <div class="news-search-div">
        <label for="search" class="news-label">
          <img src="../assets/img/search.svg" alt="" />
          <input
            type="text"
            name="text"
            placeholder="Search by ID (1-10)"
            v-model="search"
            class="news-label__input"
          />
          <button @click="deleteInput">X</button>
        </label>
      </div>
      <div class="news-list">
        <div class="news-list_recent">
          <div class="news-list_recent--one news-list_recent--item">
            <div class="news-list_recent--item__img">
              <img src="../assets/img/news/image.png" alt="" />
            </div>
            <div class="news-list_recent--item__writer">
              <h3>Iskander Akh</h3>
              <span> .5min</span>
            </div>
            <h2 class="news-list_recent--item__title">
              Interesting one-way roads in KL
            </h2>
            <p class="news-list_recent--item__text">
              "Laluan Sehala" is a Malay term that translates to "one-way road"
              in English. It refers to a road or street that is designated for
              traffic flow in only one direction. This term is commonly used in
              Malaysia and other Malay-speaking regions to indicate roads where
              vehicles are allowed to travel in a specific direction, usually
              indicated by road signs and markings. Laluan Sehala helps to
              regulate and organize traffic, ensuring smooth and efficient
              movement of vehicles along the designated route.
            </p>
          </div>
          <span class="news-line"></span>
          <div class="news-list_recent--two news-list_recent--item">
            <div class="news-list_recent--item__img">
              <img src="../assets/img/news/image1.png" alt="" />
            </div>
            <div class="news-list_recent--item__writer">
              <h3>Iskander Akh</h3>
              <span> .5min</span>
            </div>
            <h2 class="news-list_recent--item__title">
              Hotel Boss is the place where we stayed in Singapore
            </h2>
            <p class="news-list_recent--item__text">
              Hotel Boss is a popular hotel located in Singapore, and it offers
              guests a range of views from its rooms. One of the notable views
              that guests can enjoy from Hotel Boss is the view of a nearby
              mosque and cemetry. Hotel Boss in Singapore offered us a
              comfortable stay for $200 USD. The hotel provided excellent
              amenities, friendly staff, and a convenient location. We enjoyed
              our time there and found it to be a great value for the price.

              <span
                >Lorem Ipsum is simply dummy text of the printing and
                typesetting industry.
              </span>
            </p>
          </div>
        </div>
        <div class="news-list_searched" style="cursor: pointer">
          <div
            @click="getInfo(index)"
            class="news-list_searched__item"
            v-for="(article, index) in filteredPosts.slice(0, 8)"
            :key="index"
          >
            <div class="news-list_searched__item--img">
              <img :src="article.image" alt="" />
            </div>
            <div class="news-list_searched__item--text">
              <div class="news-list_searched__item--text-author">
                <h4 :class="article.color">Iskander Akh</h4>
                <span>ID: {{ article.id }}</span>
              </div>
              <h2 class="news-list_searched__item--text-title">
                {{ article.title }}
              </h2>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Contact class="container contact" />
    <Footer class="footer" />
  </div>

  <div class="modal" v-if="isModalOpen">
    <img class="modal-img" :src="picked.image" alt="">
    <div class="modal-content container">
      <div class="modal-content_text">
        <div class="news-list_searched__item--text-author">
          <h4 :class="picked.color">Iskander Akh</h4>
          <span>.5min</span>
        </div>
        <h1 class="modal-content_text--title">
          {{ picked.title }}
        </h1>
        <p class="modal-content_text--text">
          {{ picked.text }}
        </p>
      </div>
      <button class="closeModal" @click="closeModal">X</button>
    </div>
  </div>
</template>
<style scoped>
.contact {
  margin-top: 100px;
  margin-bottom: 150px;
}
</style>
<script>
import Contact from "./Contact.vue";
import Footer from "./Footer.vue";
import CurrentLink from "@/components/CurrentLink.vue"
export default {
  components: { Contact, Footer, CurrentLink },
  data() {
    return {
      index: null,
      indexOfArticle: null,
      search: "",
      isModalOpen: false,
      currentLink: 'News',
      headerText:'Here are some news from my Life that you can read',
      headerUnderText:'You can use an imput bellow to search news by ID ',
      news: [
        {
          title: "Flight from Tashkent to Kuala-Lumpur ",
          text: "This was my first flight abroad. To be honest is was uncomfortable flying in AIRBUS because of the size. However, I enjoyed every second of this flight. Departure was at night and it took 6:30 hours to get to KL. At the morning we were over Indian ocen. You can check the photo above of view from porthole",
          image: require("../assets/img/news/img1.png"),
          color: "pink",
          id: "1",
        },
        {
          title: "Chinatown in Kuala-Lumpur where we stopped",
          text: "Chinatown in Kuala Lumpur (KL) is a lively and dynamic neighborhood that captivates visitors with its vibrant atmosphere. Situated near the famous Petaling Street, this district is a hub of cultural diversity, traditional shops, and street vendors. Exploring Chinatown is a sensory delight, as the air is filled with enticing aromas of sizzling street food and the sounds of bargaining and chatter. The streets are adorned with colorful stalls, selling a wide array of goods, from textiles and accessories to local handicrafts and souvenirs. It's a treasure trove for shoppers and a paradise for food enthusiasts. From tantalizing Chinese cuisine to flavorful Malay delicacies and delicious Indian fare, the food scene in Chinatown is an enticing blend of flavors and culinary traditions. Whether you're craving a hearty plate of noodles, savory dumplings, or mouthwatering grilled satay, you'll find an abundance of choices to satisfy your taste buds. During our stay in Kuala Lumpur, we had the pleasure of experiencing the vibrant charm of Chinatown while staying at Hotel Nan-Yeang. This conveniently located hotel provided us with a comfortable base from which to explore the bustling streets and immerse ourselves in the cultural tapestry of the area. Hotel Nan-Yeang offered well-appointed rooms, friendly service, and a warm ambiance that made us feel right at home. Being just steps away from the energetic buzz of Chinatown, we were able to indulge in the sights, sounds, and flavors of this captivating neighborhood effortlessly. Each day, we would venture out to discover hidden gems, browse through an eclectic mix of goods, and relish the gastronomic delights that awaited us at every corner. From sampling delectable street food to uncovering unique trinkets and souvenirs, our stay at Hotel Nan-Yeang allowed us to fully immerse ourselves in the vibrant spirit of Chinatown, creating lasting memories of our time in Kuala Lumpur.",
          image: require("../assets/img/news/img2.png"),
          color: "blue",
          id: "2",
        },
        {
          title: 'The most beautiful mall I have ever seen "PAVILION"',
          text: "Pavilion Kuala Lumpur is a renowned shopping destination in the heart of Kuala Lumpur, Malaysia. Spanning over 1.37 million square feet, Pavilion offers a luxurious retail experience with a vast selection of international and local brands. This iconic shopping center boasts a stunning architectural design, combining contemporary elements with sleek elegance. Inside, shoppers can explore a wide range of fashion, beauty, lifestyle, and dining options, catering to every taste and preference. With its upscale ambiance, world-class retailers, and captivating events, Pavilion attracts locals and tourists alike. It is a must-visit destination for those seeking a premium shopping experience in Kuala Lumpur.",
          image: require("../assets/img/news/img3.png"),
          color: "orange",
          id: "3",
        },
        {
          title: "Petronas Towers - are the highest twin towers since 1998",
          text: "Petronas, or Petroliam Nasional Berhad, is a Malaysian multinational oil and gas company established in 1974. It is the national oil company of Malaysia, involved in exploration, production, refining, marketing, and distribution of petroleum products. Petronas is renowned for its iconic Petronas Towers, symbolizing Malaysia's economic progress. Apart from its oil and gas operations, Petronas has diversified into petrochemicals, power generation, and renewable energy. As a state-owned company, Petronas contributes significantly to Malaysia's economy, generating revenue, providing employment, and supporting development projects. It also engages in corporate social responsibility initiatives and sponsors sports and cultural events, both locally and internationally.",
          image: require("../assets/img/news/img4.png"),
          color: "blue",
          id: "4",
        },
        {
          title: "Gorgeous plants and trees are everywhere",
          text: "Kuala Lumpur, the capital city of Malaysia, is adorned with a diverse array of plants and trees, enhancing its urban landscape with natural beauty. The city's green spaces, such as parks, gardens, and tree-lined streets, are home to a variety of plant species. Towering rain trees, majestic palm trees, vibrant bougainvillea, and fragrant frangipani are commonly found throughout the city. KL's parks, like the Lake Gardens and Perdana Botanical Garden, showcase an extensive collection of tropical flora, including orchids, ferns, and exotic plants. These green havens offer respite from the bustling city and provide opportunities for leisurely walks, picnics, and appreciating the lush greenery that thrives amidst Kuala Lumpur's urban environment.",
          image: require("../assets/img/news/img5.png"),
          color: "green",
          id: "5",
        },
        {
          title: "Shining Central Business District in Singapore",
          text: "The Central Business District (CBD) in Singapore stands as a shining testament to the city-state's economic prowess and urban development. This bustling district is a hub of towering skyscrapers, modern architecture, and a dynamic business environment. The CBD showcases a seamless blend of commercial, financial, and residential spaces. The iconic Marina Bay Sands, the majestic skyline, and the vibrant waterfront promenade create a visually stunning landscape. The CBD houses multinational corporations, financial institutions, luxury hotels, high-end retail, and fine dining establishments. It is also well-connected with efficient transportation networks, making it a prime destination for business, commerce, and leisure, contributing to Singapore's status as a global economic powerhouse.",
          image: require("../assets/img/news/img6.png"),
          color: "blue",
          id: "6",
        },
        {
          title:
            "In Singapore even construction sites are looking well-groomed",
          text: "Singapore, known for its meticulous attention to detail, takes pride in its commitment to creating an aesthetically pleasing environment even in unexpected places. This is evident in the way even construction sites are well-groomed throughout the city. Instead of being eyesores, these sites are adorned with vibrant hoardings featuring stunning artwork, colorful murals, or informative displays. The aim is to transform the construction process into a visually appealing experience for residents and visitors alike. This attention to beauty extends beyond construction sites, with meticulously landscaped gardens, meticulously maintained streets, and innovative architectural designs. Singapore's dedication to creating a beautiful urban landscape reflects its commitment to providing a harmonious and visually delightful experience for all.",
          image: require("../assets/img/news/img7.png"),
          color: "purple",
          id: "7",
        },
        {
          title: "2nd and last Day in Singapore",
          text: "Singapore, beyond its architectural wonders, offers a multitude of captivating experiences for visitors. On the second day of our journey, we ventured to Marina Bay, a vibrant waterfront area that showcases the city's modernity and cultural richness. There, we marveled at the famous Water Lions fountains, also known as the Merlion, an iconic symbol of Singapore. The breathtaking water display and the majestic statue captivated our senses, providing a memorable backdrop for photos and moments of awe. As our Singapore adventure came to an end, we embarked on a bus journey to Kuala Lumpur, bidding farewell to the remarkable sights, diverse cuisine, and warm hospitality that defined our time in this extraordinary city-state.",
          image: require("../assets/img/news/img8.png"),
          color: "pink",
          id: "8",
        },
        {
          title: "Singapore is also well know for it's Gardening",
          text: "Singapore is renowned for its exceptional dedication to gardening and creating a green and sustainable environment. The city-state's commitment to horticulture is evident in its numerous well-maintained parks, lush gardens, and innovative green spaces. One of the notable attractions is the world-famous Gardens by the Bay, featuring futuristic Supertrees and magnificent conservatories showcasing a diverse range of plants from around the world. Singapore's emphasis on gardening extends beyond tourist hotspots, with meticulously landscaped streets, rooftop gardens, and community gardens that promote a sense of harmony with nature. This green philosophy not only enhances the city's aesthetic appeal but also contributes to a healthier and more livable urban environment for residents and visitors alike.",
          image: require("../assets/img/news/img9.png"),
          color: "orange",
          id: "9",
        },
        {
          title: "Flight back to Tashkent",
          text: "During our journey back to Tashkent from Kuala Lumpur, we accidentally arrived at KLIA 2 instead of KLIA 1. Realizing our mistake, we approached the airport staff for assistance. They kindly informed us that our flight departed from KLIA 1 and suggested taking the train to reach the correct terminal. Following their advice, we boarded the train and swiftly transferred from KLIA 2 to KLIA 1. Although it was a small oversight, the experience highlighted the importance of double-checking terminal information. We were grateful for the helpfulness of the airport staff and the convenience of the train connection, which allowed us to rectify our mistake and reach the correct terminal on time for our flight back to Tashkent.",
          image: require("../assets/img/news/img10.png"),
          color: "green",
          id: "10",
        },
      ],
      picked: {
        title: "",
        text: "",
        image: require("../assets/img/news/img4.png"),
        color: "",
      },
    };
  },

  computed: {
    filteredPosts() {
      return this.news.filter((post) =>
        post.id.toLowerCase().includes(this.search.toLowerCase())
      );
    },
  },

  methods: {
    getInfo(index) {
      
      const selectedPost = this.filteredPosts[index];
      const originalIndex = this.news.findIndex(
        (post) => post.title === selectedPost.title
      );
      this.isModalOpen = true;
      this.picked.title = selectedPost.title;
      this.picked.text = selectedPost.text;
      this.picked.image = require(`../assets/img/news/img${
        originalIndex + 1
      }.png`);
      this.picked.color = selectedPost.color;
      // Wait for the modal to open before scrolling
      this.$nextTick(() => {
        window.scrollTo({
          top: 150,
          behavior: "smooth",
        });
      });
    },

    closeModal() {
      this.isModalOpen = false;
    },

    deleteInput() {
      this.search = "";
    },
  },
};
</script>


<style lang="scss">
