            border-radius: 20px;
          }
        }
        &--text {
          padding: 30px 0 0 30px;
          display: flex;
          flex-direction: column;
          gap: 23px;
          &-author {
            display: flex;
            gap: 10px;
            h4 {
              padding: 7px 25px;
              color: #fff;
              font-family: Poppins;
