              font-style: normal;
              font-weight: 400;
              line-height: 26px;
            }
            span {
              display: flex;
              align-items: center;
              justify-content: center;
              color: #fff;
              font-family: Poppins;
              font-size: 14px;
              font-style: normal;
              font-weight: 400;
              line-height: 26px;
            }
          }
          &-title {
            color: #fff;
            font-family: Poppins;
            font-size: 23px;
            font-style: normal;
            font-weight: 600;
            line-height: normal;
          }
        }
      }
    }
  }
  &-title {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 15px;
    margin-bottom: 75px;
    h5 {
      color: #fff;
      font-family: Poppins;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 33px;
      span {
        color: #e47500;
        font-family: Poppins;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 208.5%;
      }
    }
    h1 {
      color: #fff;
      text-align: center;
      font-family: Poppins;
      font-size: 33px;
      font-style: normal;
      font-weight: 600;
      line-height: 60px;
    }
    p {
      color: #fff;
      text-align: center;
      font-family: Poppins;
      font-size: 16px;
      font-style: normal;
      font-weight: 500;
      line-height: 30px;
    }
  }
  &-label {
    max-width: 400px;
    padding: 0 10px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    img {
      position: absolute;
      left: 30px;
    }
    &__input {
      border: none;
      outline: none;
      padding: 9px 10px 9px 50px;
      width: 100%;
      border-radius: 10px;
      background: #1c140f;
      height: 49px;
      color: #fff;
      // text-align: center;
      font-family: Poppins;
      font-size: 16px;
