      justify-content: flex-start;
      gap: 15px;
      &--title {
        color: #fff;
        font-family: Poppins;
        font-size: 30px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
      }
      &--text {
        color: #fff;
        font-family: Poppins;
        font-size: 17px;
