        font-weight: 400;
        line-height: 207.8%; /* 35.326px */
      }
    }
    &_img {
      height: 450px;
      width: 100%;
      img {
        max-width: 500px;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

@media (max-width: 1200px) {
  .news-list {
    flex-direction: column-reverse;
    &_recent {
      margin-top: 60px;
    }
  }
  .modal-content {
    // backdrop-filter: blur(40px);
    // box-shadow: 0px -1px 13px 100px rgba(34, 60, 80, 0.17);
    // background: rgb(0, 0, 0, 0.5);
