  }
  .news-title p {
    font-size: 12px;
  }
}
@media (max-width: 768px) {
  .news-list {
    flex-direction: column-reverse;
    &_recent {
      margin-top: 60px;
    }
  }
  .modal-content {
    flex-direction: column;
  }
  .news-title h1 {
    font-size: 18px;
    line-height: 40px;
  }
  .news-title p {
    font-size: 12px;
  }
}
@media (max-width: 480px) {
  .news-list_searched__item {
    flex-direction: row-reverse;
    align-items: center;
    gap: 10px;
    &--img {
      max-width: 130px;
      height: 100px;
      display: flex;
      align-items: center;
      justify-content: center;
