<template>
  <div class="contact">
    <div class="contact-text">
      <h1>Stay Connected</h1>
      <p>
        Stay Updated: Reach Out for Important Notifications. For inquiries and to receive notifications
      </p>
    </div>
    <div class="stayin">
      <div class="stayin-text">
        <h2>Stay in the loop</h2>
        <p>
          Subscribe to receive the latest news and updates about TDA. We promise
          not to spam you!
        </p>
      </div>
      <div class="stayin-input">
        <input
          class="stayin-input__input"
          type="email"
          name="email"
          placeholder="Enter email address"
          id=""
          v-model="email"
        />
        <button class="stayin-input__btn" @click="setItems">Continue</button>
      </div>
    </div>
  </div>
</template>

<script>
import { addDoc, collection } from "firebase/firestore";
import {db} from "../firebase"
export default {
  data() {
    return {
      email: "",
      isValid: false,
    };
  },
  methods: {
    setItems() {
      if (this.email.includes("@")) {
        this.isValid = true;
        if (this.isValid) {
          const userData = this.email;
          this.email = "";
          addDoc(collection(db, "email"), {
            email: userData,
          });
          alert("Succesfully subscribed! Thank's for your support");
          return localStorage.setItem("email", JSON.stringify(userData));
        }
      } else {
        alert("Your email has to contain @ in it");
      }
    },
  },
};
</script>

<style lang="scss">
@media (max-width: 540px) {
  
  .contact-text{
    h1{
      font-size: 20px !important;
    }
    p{
      font-size: 12px !important;
    }
  }
  .stayin{
    padding: 40px 50px 90px 50px !important;
    height: 120% !important;
    gap: 25px;
    &-text{
      h2{
        font-size: 20px !important;
      }
      p{
        font-size: 14px !important;
      }
    }
  }
}
</style>